<template>
  <div class="settings-page">
    <h1>알림 설정</h1>
  <!-- ul.notification-list>li.notification-item>span -->
  <ul class="notification-list">
      <!-- 알림 설정 항목들 -->
      <li class="notification-item">
        <span>새 메시지 알림</span>
        <ToggleSwitch v-model="settings.newMessageAlert" />
        <!-- v-model="settings.newMessageAlert"는 
        양방향 데이터 바인딩을 설정하여, 
        settings.newMessageAlert 값이 변경되면 ToggleSwitch의 상태가 자동으로 업데이트되고, 
        반대로 ToggleSwitch에서 상태를 변경하면 settings.newMessageAlert 값도 자동으로 업데이트됩니다. -->
      </li>
      <li class="notification-item">
        <span>메시지 알림</span>
        <ToggleSwitch v-model="settings.messageAlert" />
      </li>
      <li class="notification-item">
        <span>소리</span>
        <ActiveToggleSwitch v-model="settings.sound" />
      </li>
      <li class="notification-item">
        <span>진동</span>
        <ActiveToggleSwitch v-model="settings.vibration" />
      </li>
      <li class="notification-item">
        <span>진동 패턴</span>
        <ToggleSwitch v-model="settings.vibrationPattern" />
      </li>
      <li class="notification-item">
        <span>알림 표시</span>
        <ToggleSwitch v-model="settings.notificationDisplay" />
      </li>
      <li class="notification-item">
        <span>키워드 알림</span>
        <ToggleSwitch v-model="settings.keywordAlert" />
      </li>
      <li class="notification-item">
        <span>답장 메시지 알림</span>
        <ToggleSwitch v-model="settings.replyMessageAlert" />
      </li>
      <li class="notification-item">
        <span>보이스룸 알림</span>
        <ActiveToggleSwitch v-model="settings.voiceRoomAlert" />
      </li>
    </ul>
<!-- 저장 버튼 -->
  <!-- .button-container>button.save-button -->
  <div class="button-container">
    <button class="save-button" @click="saveSettings">
    저장
    </button>
    </div>
<!-- 저장 팝업 -->
    <!-- v-if와 @click.stop
    Vue.js에서 조건부 렌더링과 이벤트 처리에 사용되는 디렉티브 
    조건부 렌더링을 처리하는 Vue.js의 디렉티브입니다. 
    특정 조건이 true일 때만 해당 HTML 요소를 렌더링 -->
<div class="popup-overlay" v-if="showPopup" @click="closePopup">
  <div class="popup" @click.stop>
    <p>설정이 저장되었습니다.</p>
    <button class="popup-close-button" @click="closePopup"> 확인 </button>
  </div>
</div>
  </div>
</template>

<script>
  
  import ToggleSwitch from "./components/ToggleSwitch.vue";
  import ActiveToggleSwitch from "./components/ActiveToggleSwitch.vue";

  export default {
    // 컴포넌트를 등록 
    components: {
      ToggleSwitch,   
      ActiveToggleSwitch, 
    },
    
    // data 함수는 컴포넌트의 상태(state)와 데이터를 정의합니다.
    data() {
      return {
        // settings 객체는 사용자 설정을 저장하는 곳입니다.
        settings: {
          newMessageAlert: false, // 새 메시지 알림 상태 (기본값: false)
          messageAlert: false, // 일반 메시지 알림 상태 (기본값: false)
          sound: true, // 소리 알림 설정 (기본값: true)
          vibration: true, // 진동 알림 설정 (기본값: true)
          vibrationPattern: false, // 진동 패턴 설정 (기본값: false)
          notificationDisplay: false, // 알림 표시 여부 (기본값: false)
          keywordAlert: false, // 키워드 알림 설정 (기본값: false)
          replyMessageAlert: false, // 답장 메시지 알림 설정 (기본값: false)
          voiceRoomAlert: true, // 보이스룸 알림 설정 (기본값: true)
        },
        
        // showPopup 변수는 팝업의 표시 여부를 제어합니다.
        showPopup: false, // 팝업을 숨김 상태로 시작
      };
    },

    // methods 객체는 컴포넌트에서 사용할 메서드를 정의합니다.
    methods: {
      // saveSettings 메서드는 설정을 저장하고, 팝업을 표시하는 역할을 합니다.
      saveSettings() {
        console.log("저장된 설정:", this.settings); // 콘솔에 현재 설정값 출력
        this.showPopup = true; // 팝업을 표시하려면 true로 설정
      },
      
      // closePopup 메서드는 팝업을 닫는 역할을 합니다.
      closePopup() {
        this.showPopup = false; // 팝업을 숨기려면 false로 설정
      },
    },
  };
</script>


<style scoped>
