  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}

.save-button {
  padding: 15px 40px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 50px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.save-button:hover {
  background-color: #0E7C12;
}

.save-button:active {
  background-color: #3A3A3A;
}

/* 팝업 오버레이 */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); /* 검정 배경, 투명도 70% */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* 다른 콘텐츠 위에 표시 */
  cursor: pointer;
}

/* 팝업 박스 */
.popup {
  background-color: white;
  padding: 20px 40px;
  border-radius: 10px;
  text-align: center;
  font-size: 18px;
  color: #333;
}

.popup p {
  margin: 0;
  margin-bottom: 20px;
}

/* 팝업 확인 버튼 */
.popup-close-button {
  padding: 10px 40px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 25px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.popup-close-button:hover {
  background-color: #0E7C12;
}

.popup-close-button:active {
  background-color: #3A3A3A;
}
</style>

